import React, { Component } from 'react';

export default class Home extends Component {

  render()  {
    return (
      <div>
      <a name="about"></a>
      <div className="intro-header">
          <div className="container">

                        <div className="row">
                            <div className="col-lg-12">
                                <div className="intro-message">
                                    <h1>Tabriz Events</h1>
                                    <h3>Events for Nodejs , Julia and etc</h3>
                                    <hr className="intro-divider"/>
                                    <ul className="list-inline intro-social-buttons">
                                        <li>
                                            <a href="#" className="btn btn-default btn-lg"><i className=""></i> <span className="network-name">Site is under construction</span></a>
                                        </li>
                                        <li>
                                            <a href="https://github.com/tabriz/" className="btn btn-default btn-lg"><i className="fa fa-github fa-fw"></i> <span className="network-name">Github</span></a>
                                        </li>

                                    </ul>
                                </div>
                            </div>
                        </div>

          </div>

      </div>
      </div>


    );
  }

}



